import React, {Component} from "react";
import "./WithFooter.css";
import Footer from "./Footer.js";
import { Redirect } from "react-router";
import { Button } from "@blueprintjs/core";
import { useHistory } from 'react-router-dom';

export default function WithFooter (ComponentToProtect) {
    let history = useHistory();
    return class extends Component {
        constructor () {
            super();
            this.state = {
                loading: true,
                redirect: false,
            };
        }

        componentDidMount () {
            this.setState({loading: false});
        }
 
        render () {
            const { loading, redirect } = this.state;

            if (loading) {
                return null;
            }
            if (redirect) {
                return <Redirect to="/" />;
            }
  
            return (
                <div className="footer-div">
                    <div className="top-section">
                        <div className="image-section"></div>
                        <h1 className="bp3-heading">OPOS</h1>
                        <div className="withfooter_signout-section">
                            <Button className="withfooter_signout-button"
                            minimal={true}
                            text="Sign In"
                            onClick={() => history.push("/")}></Button>
            </div>
                    </div>
                    <ComponentToProtect {...this.props} /> 
                    <Footer />
                </div>
            );
        }
    }
}

