import * as React from "react";
import './App.css';
import LoginPage from './components/LoginPage.js';
import WithNavigation from "./components/WithNavigation.js";
import AboutUs from "./components/AboutUs.js";
import WithFooter from "./components/WithFooter.js";
import CorporateSite from "./components/CorporateSite.js";
import HelpAndAdvice from "./components/HelpAndAdvice.js";

import {
    Route,
    Switch
} from 'react-router-dom';
import ContactUs from "./components/ContactUs";
import RealtimeChat from "./components/RealtimeChat";
import HomePage from "./components/HomePage.js";
import ViewAccount from "./components/ViewAccount.js";
import MakePaymentPlan from "./components/MakePaymentPlan";
import MakePayment from "./components/MakePayment";
import DownloadStatement from "./components/DownloadStatement";
import RequestCallback from "./components/RequestCallback";
import DownloadMandate from "./components/DownloadMandate";
import IncomeAndExpenditure from "./components/IncomeAndExpenditure";

function App() {
    return (
        
            <Switch>
                <Route key={"home"} path="/" component={WithFooter(LoginPage)}  exact>
                </Route>
                <Route path="/aboutus" component={WithFooter(AboutUs)} exact>
                </Route>    
                <Route path="/corporatesite" component={WithFooter(CorporateSite)} exact>
                </Route> 
                <Route path="/contactus" component={WithFooter(ContactUs)} exact>
                </Route>  
                <Route path="/realtimechat" component={WithFooter(RealtimeChat)} exact>
                </Route> 
                <Route path="/homepage" component={WithNavigation(HomePage)} exact>
                </Route>  
                <Route path="/helpandadvice" component={WithNavigation(HelpAndAdvice)} exact>
                </Route>  
                <Route path="/viewaccount" component={WithNavigation(ViewAccount)} exact>
                </Route>  
                <Route path="/makeapayment" component={WithNavigation(MakePayment)} exact>
                </Route> 
                <Route path="/makepaymentplan" component={WithNavigation(MakePaymentPlan)} exact>
                </Route> 
                <Route path="/downloadstatement" component={WithNavigation(DownloadStatement)} exact>
                </Route>  
                <Route path="/requestcallback" component={WithNavigation(RequestCallback)} exact>
                </Route> 
                <Route path="/downloadmandate" component={WithNavigation(DownloadMandate)} exact>
                </Route>  
                <Route path="/incomeandexpenditure" component={WithNavigation(IncomeAndExpenditure)} exact>
                </Route>                                              
            </Switch>
        
    );
}

export default App;
