import React, {useState} from "react";
import { Button, 
        FormGroup, 
        InputGroup, 
        NumericInput, 
        Radio, 
        RadioGroup,
        Card,
        Elevation} from '@blueprintjs/core';
import { useHistory } from "react-router-dom";     
import '@blueprintjs/core/lib/css/blueprint.css';
import "./LoginPage.css";
import { Classes, Tooltip2 } from "@blueprintjs/popover2";

function LoginWithOtp ({history}) {
    //let history = useHistory();
    //let {history} = props;

    const infoButton = (
            <Tooltip2 className={Classes.TOOLTIP2_INDICATOR}
                content={<span>Your reference number and PIN will be on any letter we send to you.</span>}
                placement="right"
                intent="danger"
                minimal={false}
                usePortal={true}
            >
                <Button icon="info-sign" minimal={true}/>
        </Tooltip2>
    );
    let [opNumber, setOpNumber] = useState(); 
    let [pin, setPin] = useState(); 
    let [userInfo, setUserInfo] = useState({})
    let [message, setMessage] = useState('');
    const handleusersignin = async () => {
        console.log(opNumber)
        const result = await fetch(`/api/users/login/${opNumber}/${pin}`, {
                method: 'POST'
        });
        const body = await result.json();
        setMessage(body.message);
        if (body.message === 'success') {
            history.push("/homepage");
        }
        //console.log(body);
    }

    return (
        <div className="flex-container-otp">
            <FormGroup
                label="OP Reference Number"
                labelFor="text-input-opnumber"
                labelInfo="(required)"
            >
                <InputGroup 
                    id="text-input-opnumber" 
                    placeholder="OP123456"
                    rightElement={infoButton}
                    value={opNumber}
                    onChange = {(event) => {
                        //console.log(event.target.value)
                        setOpNumber(event.target.value)
                    } }/>
            </FormGroup>
            <FormGroup
                label="Pin"
                labelFor="text-input-pin"
            >
                <NumericInput 
                    className="input-pinnumber" 
                    placeholder="1234"
                    value={pin}
                    onValueChange = {(pinValue, pinString) => {
                        console.log(pinValue)
                        setPin(pinValue)
                    } }
                />
            </FormGroup>
            <div className="message-section">{message}</div>
            <div className="loginButton">
                <Button id="login-button-otp" 
                        text="Sign In"
                        rightIcon="log-in"
                        onClick={() => {
                            //history.push("/homepage");
                            handleusersignin();
                        }}>
                </Button>
                
            </div>
            <br/>
        </div>
    )
        

}

function LoginWithDetails ({history}) {
    return (
        <div className="flex-container-details">
            <FormGroup
                label="Name"
                labelFor="text-input-name"
            >
                <InputGroup id="text-input-name" />
            </FormGroup>
            <FormGroup
                label="Date of Birth"
                labelFor="text-input-dob"
            >
                <InputGroup id="text-input-dob" placeholder="dd/mm/yyyy"/>
            </FormGroup>
            <FormGroup
                label="House Number/Name"
                labelFor="text-input-housenum"
            >
                <InputGroup id="text-input-housenum" />
            </FormGroup>
            <FormGroup
                label="Post Code"
                labelFor="input-postcode"
            >
                <InputGroup id="input-postcode" />
            </FormGroup>
            <FormGroup
                label="Email address/Phone Number"
                labelFor="input-housenum"
            >
                <InputGroup id="input-emailorphone" />
            </FormGroup>
            <div className="loginButton">
                <Button id="login-button-details" text="Sign In" rightIcon="log-in" />
            </div>    
            <br/>
            <br />
        </div>
    );
}

const LoginPage = () => {
    let [isOTPLogin, setIsOTPLogin] = useState(true);
    let history = useHistory();

    return (
        <div className="loginPage">
            <Card className="flex-container" interactive={true} elevation={Elevation.FOUR}>
                <div className="sign-in-text">
                    <h2 className="login-heading">Sign In</h2>
                </div>    
                <RadioGroup
                    inline={true}
                    name="group" selectedValue={isOTPLogin ? "one" : "two"}
                    onChange={() => setIsOTPLogin(!isOTPLogin)}
                >
                    <Radio label="Login with OTP" value="one" />
                    <Radio label="Login with Details" value="two" />
                </RadioGroup>

                <br />
                <br />
                {
                    isOTPLogin ? <LoginWithOtp history={history}/> : <LoginWithDetails />
                }
            </Card>
        </div>
    );
}

export default LoginPage;